import { FC, MouseEvent, useEffect, useLayoutEffect, useRef, useState } from 'react';

import {
  Breadcrumbs,
  Flex,
  getEdge,
  Icon,
  registerIcon,
  Text,
  Link,
  MenuItemProps,
  Button,
  MenuButton,
  throttle,
  useDirection,
  useI18n,
  useAfterInitialEffect
} from '@pega/cosmos-react-core';
import * as pencilIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/pencil.icon';
import * as moreIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/more.icon';

import {
  StyledCaseHeader,
  StyledCaseIconWrap,
  StyledCaseHeaderText,
  StyledExpandCollapseSummaryButton,
  StyledSubheading,
  StyledHeaderActions,
  StyledCaseHeaderPromotedAction
} from '../CaseView.styles';
import { useCaseViewContext } from '../CaseView.context';

import Summary from './Summary';

registerIcon(pencilIcon, moreIcon);

const CaseHeader: FC = () => {
  const t = useI18n();
  const { ltr } = useDirection();

  const {
    testIds,
    caseId,
    icon,
    heading,
    subheading,
    actions,
    caseLink,
    parentCases,
    onEdit,
    followed,
    onFollowedChange,
    promotedActions,
    summaryExpanded,
    summaryFields,
    onToggleSummary,
    isPreview,
    aboveSM,
    aboveMD,
    persistentUtility
  } = useCaseViewContext();

  const [wrapPromotedActions, setWrapPromotedActions] = useState(false);
  const [summaryResetID, setSummaryResetID] = useState<number | undefined>(Math.random());

  const wrapActionsBreakpoint = useRef<number | null>(null);
  const contentsEl = useRef<HTMLDivElement>(null);
  const headerGroupEl = useRef<HTMLElement>(null);
  const actionsContainerEl = useRef<HTMLDivElement>(null);

  const caseActions: MenuItemProps[] | undefined = actions
    ? actions.map(action => {
        return {
          ...action,
          primary: action.text,
          visual: action.icon ? <Icon name={action.icon} /> : null
        };
      })
    : undefined;

  const toggleButtonRef = useRef<HTMLButtonElement>(null);

  // FIXME: This is a hack to get the Tooltip to reset its position when the summary changes layout.
  useAfterInitialEffect(() => {
    if (toggleButtonRef.current && toggleButtonRef.current === document.activeElement) {
      toggleButtonRef.current.blur();
      setTimeout(() => {
        toggleButtonRef.current?.focus();
      }, 0);
    }
  }, [summaryExpanded]);

  const showExpandCollapse = !isPreview && aboveMD;
  const collapsedMainHeader =
    (!isPreview && !aboveMD && aboveSM) || (showExpandCollapse && !summaryExpanded);

  // Actions wrapping
  useLayoutEffect(() => {
    if (
      !wrapPromotedActions &&
      contentsEl.current &&
      headerGroupEl.current &&
      actionsContainerEl.current
    ) {
      const contentStart = getEdge({
        el: contentsEl.current,
        side: 'leading',
        ltr
      });
      const headerGroupEnd = getEdge({
        el: headerGroupEl.current,
        side: 'trailing',
        ltr
      });
      const actionsContainerWidth = actionsContainerEl.current.getBoundingClientRect().width;

      wrapActionsBreakpoint.current =
        Math.ceil(headerGroupEnd) - Math.ceil(contentStart) + Math.ceil(actionsContainerWidth);
    }

    const promotedActionsWrapHandler = () => {
      if (wrapActionsBreakpoint.current && contentsEl.current) {
        setWrapPromotedActions(
          Math.ceil(contentsEl.current.offsetWidth) < wrapActionsBreakpoint.current
        );
      }
    };

    const resizeObserver = new ResizeObserver(throttle(promotedActionsWrapHandler, 30));

    promotedActionsWrapHandler();

    if (contentsEl.current) {
      resizeObserver.observe(contentsEl.current, { box: 'border-box' });
    }

    return () => {
      resizeObserver.disconnect();
    };
  }, [
    contentsEl,
    headerGroupEl,
    actionsContainerEl,
    promotedActions,
    heading,
    subheading,
    wrapPromotedActions
  ]);

  // Reset summary
  useEffect(() => {
    setSummaryResetID(Math.random());
  }, [heading, summaryFields, promotedActions]);

  return (
    <Flex
      data-testid={testIds.header}
      as={StyledCaseHeader}
      container={{ direction: 'column', gap: 1.5 }}
    >
      <Flex container ref={contentsEl}>
        {showExpandCollapse && onToggleSummary && !persistentUtility && (
          <StyledExpandCollapseSummaryButton
            ref={toggleButtonRef}
            onClick={onToggleSummary}
            label={t(summaryExpanded ? 'collapse' : 'expand')}
            aria-label={t(summaryExpanded ? 'collapse_summary' : 'expand_summary')}
            icon
            compact
          >
            <Icon name='arrow-micro-left' />
          </StyledExpandCollapseSummaryButton>
        )}

        {icon && (
          <Flex container item={{ shrink: 0 }} as={StyledCaseIconWrap}>
            {icon}
          </Flex>
        )}

        {/* header group */}
        <Flex
          as={StyledCaseHeaderText}
          container={{
            direction: 'column',
            pad: [0.5, 2, 0.5, 1.5],
            alignItems: 'start'
          }}
          item={aboveSM ? { shrink: wrapPromotedActions ? 1 : 0 } : { shrink: 1, grow: 1 }}
          ref={headerGroupEl}
        >
          <Text data-testid={testIds.heading} variant='h1'>
            {caseLink ? <Link {...caseLink}>{heading}</Link> : heading}
          </Text>

          {subheading && (!parentCases || subheading !== caseId) && (
            <StyledSubheading data-testid={testIds.subheading}>{subheading}</StyledSubheading>
          )}

          {parentCases && (
            <Breadcrumbs
              forwardedAs='p'
              path={[
                ...parentCases,
                {
                  ...caseLink,
                  id: caseId,
                  primary: caseId,
                  href: caseLink?.href
                }
              ]}
              leafOnly
            />
          )}
        </Flex>

        {/* summary */}
        {collapsedMainHeader && !wrapPromotedActions && (
          <Summary siblingRef={actionsContainerEl} key={summaryResetID} />
        )}

        {/* inline actions */}
        <Flex
          as={StyledHeaderActions}
          item={{ shrink: 0 }}
          container={{ alignItems: 'start', pad: [0, 0, 0, 2] }}
          offsetEnd={!summaryExpanded}
          ref={actionsContainerEl}
        >
          {collapsedMainHeader &&
          !wrapPromotedActions &&
          promotedActions &&
          promotedActions.length > 0 ? (
            <>
              {promotedActions.map(promotedAction => (
                <StyledCaseHeaderPromotedAction
                  variant='secondary'
                  onClick={(e: MouseEvent<HTMLButtonElement>) =>
                    promotedAction.onClick?.(promotedAction.id, e)
                  }
                  key={promotedAction.id}
                >
                  {promotedAction.text}
                </StyledCaseHeaderPromotedAction>
              ))}
            </>
          ) : null}

          {onEdit && (
            <Button
              data-testid={testIds.edit}
              icon
              variant='simple'
              onClick={onEdit}
              label={t('edit')}
            >
              <Icon name='pencil' />
            </Button>
          )}

          {caseActions && (
            <MenuButton
              data-testid={testIds.actions}
              icon='more'
              iconOnly
              variant='simple'
              text={t('actions')}
              menu={{
                items:
                  followed !== undefined
                    ? [
                        {
                          id: 'follow',
                          primary: followed ? t('unfollow') : t('follow'),
                          onClick: () => onFollowedChange?.(!followed)
                        },
                        ...caseActions
                      ]
                    : caseActions,
                scrollAt: 20
              }}
            />
          )}
        </Flex>
      </Flex>

      {/* wrapped promoted actions */}
      {(!collapsedMainHeader || wrapPromotedActions || !aboveSM) &&
      promotedActions &&
      promotedActions.length > 0 ? (
        <Flex
          data-testid={testIds.promotedActions}
          container={{ justify: 'start', wrap: 'wrap', gap: 1, pad: [0, 0, 0.5] }}
        >
          {promotedActions.map(({ id, text, onClick }) => (
            <div key={text}>
              <StyledCaseHeaderPromotedAction
                variant='secondary'
                onClick={(e: MouseEvent<HTMLButtonElement>) => onClick?.(id, e)}
              >
                {text}
              </StyledCaseHeaderPromotedAction>
            </div>
          ))}
        </Flex>
      ) : null}
    </Flex>
  );
};

export default CaseHeader;
